import { IIdFormulario } from '@/dominio/comum/IdFormulario';
import { RamificacaoDTO } from '@/dominio/especificacao/EspecificacaoDTO';

export interface IRamificacao {
    getIrPara(): 'avanar' | 'fim do formulrio' | IIdFormulario;
    setIrPara(
        ramificacao: 'avanar' | 'fim do formulrio' | IIdFormulario,
    ): void;
    gerarEspecificacao(): RamificacaoDTO;
}

export class Ramificacao implements IRamificacao {
    constructor(
        private irPara:
            | 'avanar'
            | 'fim do formulrio'
            | IIdFormulario = 'avanar',
    ) {}

    getIrPara(): IIdFormulario | 'avanar' | 'fim do formulrio' {
        return this.irPara;
    }

    setIrPara(ramificacao: IIdFormulario | 'avanar' | 'fim do formulrio') {
        this.irPara = ramificacao;
    }

    gerarEspecificacao(): RamificacaoDTO {
        return {
            irPara: this.irPara.toString(),
        };
    }
}
