import { ISubtitulo, Subtitulo } from './Subtitulo';

export interface ISubtituloFactory {
    /**
     * Cria um objeto do tipo Subtitulo. Espera receber como parmetro uma string vlida, caso contrrio lana uma exceo
     * @param {string} subtitulo subttulo com trs ou mais caracteres
     * @throws {ErroSubtituloEmBranco} se parmetro for uma string vazia
     * @throws {ErroSubtituloComMenosDeTresCaracteres} se parmetro for uma string com menos de trs caracteres
     */
    criar(subtitulo: string): ISubtitulo;
}

export class SubtituloFactory implements ISubtituloFactory {
    criar(subtitulo: string) {
        return new Subtitulo(subtitulo);
    }
}
